* {
  margin: 0;
  padding: 0; }

body {
  color: #000000;
  font-size: 16px; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

ul, ol {
 margin: 0; }

li {
  list-style: none; }

a {
  &:hover {
    text-decoration: none; } }

button {
  border: none;
  cursor: pointer; }

figure {
  margin: 0; }


::-moz-selection {
  background: #ffffff;
  color: black; }
::selection {
  background: #ffffff;
  color: black; }
