.Card {
  box-shadow: 0 0 14px 0px #e4e4e4;
  &__figure {
    position: relative;
    overflow: hidden; }
  &__tipo-linea {
    background: $color-negro;
    color: $color-blanco;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .tipo-picada-title {
      font-family: $lato-regular;
      @extend %capi;
      font-size: 15px; }
    a {
      font-size: 15px;
      color: $color-blanco;
      transition: color .3s $ease-links;
      &:hover {
        color: darken($color-blanco, 35%); } } }

  &__info {
    padding: 15px 15px 25px 15px;
    .picada-nombre {
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-family: $lato-bold;
      margin-bottom: 10px;
      h3 {
        @extend %capi;
        font-size: 22px; }
      span.precio-card {
        font-size: 25px;
        text-decoration: line-through; } }
    .picada-tamano {
      position: relative;
      font-family: $lato-bold;
      padding-bottom: 15px;
      border-bottom: 1px solid $color-negro;
      @extend %capi;
      &:before {
        content: '';
        display: inline-block;
        background: url(../img/cards/persona.svg);
        -webkit-background-size: cover;
        background-size: cover;
        width: 17px;
        height: 17px;
        margin-right: 7px; } }
    .picada-pedir-precios {
      text-align: center;
      .descuento {
        margin-bottom: 10px;
        font-family: $lato-bold;
        span.off {}
        span.tipo-entrega {
          @extend %upper; } }
      a.btn-pedi-card {
        @extend %but;
        width: 100%;
        padding: 2px 0;
        span {
          font-family: $lato-bold;
          font-size: 23px; } } } } }


